import React from 'react';
import './AboutUs.css';
import { useContext } from "react";
import { LanguageContext } from '../LanguageContext';

export default function AboutUs() {
    const { language } = useContext(LanguageContext);

    return (
        <section id="AboutUs" className="about-us-container">
            <div className="about-us-content">
                {language === "serbian" ? (
                    <>
                        <h1 className="text-center mb-4">O nama</h1>
                        <p className="about-us-text">
                            Mi smo tim vizionara i strunjaka posveenih stvaranju inovativnih reenja koja oblikuju budunost.
                            Kroz saradnju i kreativnost, postavljamo nove standarde u industriji i pomaemo naim klijentima
                            da postignu svoje ciljeve.
                        </p>
                        <p className="about-us-text">
                            Naa misija je jasna  transformisati ideje u stvarnost i kreirati odriva reenja koja donose vrednost
                            za sve strane ukljuene u proces.
                        </p>
                    </>
                ) : language === "english" ? (
                    <>
                        <h1 className="text-center mb-4">About us</h1>
                        <p className="about-us-text">
                            We are a team of visionaries and experts dedicated to creating innovative solutions that shape the future.
                            Through collaboration and creativity, we set new standards in the industry and help our clients
                            achieve their goals.
                        </p>
                        <p className="about-us-text">
                            Our mission is clear  to transform ideas into reality and create sustainable solutions that bring value
                            to all parties involved in the process.
                        </p>
                    </>
                ) : language === "german" ? (
                    <>
                        <h1 className="text-center mb-4">ber uns</h1>
                        <p className="about-us-text">
                            Wir sind ein Team von Visionren und Experten, das sich der Schaffung innovativer Lsungen widmet, die die
                            Zukunft gestalten. Durch Zusammenarbeit und Kreativitt setzen wir neue Mastbe in der Branche und helfen
                            unseren Kunden, ihre Ziele zu erreichen.
                        </p>
                        <p className="about-us-text">
                            Unsere Mission ist klar  Ideen in die Realitt umzusetzen und nachhaltige Lsungen zu schaffen, die allen
                            Beteiligten im Prozess einen Mehrwert bringen.
                        </p>
                    </>
                ) : (
                    <>
                        <h1 className="text-center mb-4">About us</h1>
                        <p className="about-us-text">
                            We are a team of visionaries and experts dedicated to creating innovative solutions that shape the future.
                            Through collaboration and creativity, we set new standards in the industry and help our clients
                            achieve their goals.
                        </p>
                        <p className="about-us-text">
                            Our mission is clear  to transform ideas into reality and create sustainable solutions that bring value
                            to all parties involved in the process.
                        </p>
                    </>
                )}
            </div>
        </section>
    );
}
